* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fff;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

a {
  color: inherit;
  text-decoration: none;
}

.search {
  width: 100%;
  z-index: 10;
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 0 20px;
  display: flex;
  position: fixed;
}

.search-fixed {
  box-shadow: 0 2px 8px 1px rgba(65, 105, 225, .9);
}

.search-form {
  width: 500px;
  height: 40px;
  border: 1px solid #4169e1;
  border-radius: 20px;
  outline: none;
  padding: 0 15px;
  position: relative;
}

.search-form:hover {
  box-shadow: 0 2px 8px 1px rgba(65, 105, 225, .9);
}

@media screen and (max-width: 540px) {
  .search-form {
    width: 280px;
  }
}

input {
  height: 35px;
  width: 87%;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
}

.search-btn {
  height: 34px;
  width: 70px;
  color: #fff;
  cursor: pointer;
  background-color: #4169e1;
  border: none;
  border-radius: 50px;
  font-size: small;
  position: absolute;
  top: 2px;
  right: 2px;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 150px 10px 10px;
  display: grid;
}

.gallery__item {
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.photo-card {
  position: relative;
  overflow: hidden;
}

.photo-card:hover .info {
  transform: translateY(0);
}

.info {
  width: 100%;
  color: #4169e1;
  text-align: center;
  opacity: 1;
  background-color: rgba(15, 15, 15, .7);
  justify-content: space-between;
  padding: 5px 20px;
  font-size: 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(120%);
}

.info-item {
  margin: 0;
  padding: 0;
}

.info-item-value {
  color: #fff;
  font-size: 8px;
}

.load-more {
  height: 50px;
  width: 50px;
  color: #fff;
  cursor: pointer;
  background-color: #4169e1;
  border: none;
  border-radius: 50px;
  margin: 50px auto;
  font-size: small;
  display: none;
}

.hiden {
  display: block;
}

.dark {
  background-color: #212121;
}

.them-wrapper, .reser-wrapper {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.them-l, .them-d, .history-btn, .reset, .dark-h {
  height: 34px;
  width: 34px;
  color: #fff;
  cursor: pointer;
  background-color: #4169e1;
  border: 1px solid #4169e1;
  border-radius: 100px;
  font-size: large;
  display: block;
}

.them-l:hover, .them-d:hover, .history-btn:hover, .reset:hover, .load-more:hover {
  color: #4169e1;
  background-color: #fff;
  border: 1px solid #4169e1;
  box-shadow: 0 2px 8px 1px rgba(65, 105, 225, .9);
}

.them-d {
  display: none;
}

.features {
  width: 100px;
  justify-content: space-around;
  padding-top: 10px;
  display: flex;
}

.features div:not(:last-child) {
  margin-right: 10px;
}

.dark-h:hover, .input-d {
  color: #fff;
  background-color: #303134;
}

.hidden {
  visibility: hidden;
}

.start-page__upepr-title {
  color: #4169e1;
  text-transform: uppercase;
  margin: 0;
  font-size: 100px;
}

.start-page {
  color: #4169e1;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hide {
  display: none;
}

.start-page__title {
  width: 100%;
  text-align: center;
  animation: scale 3s cubic-bezier(.5, 1, .89, 1) forwards;
  transform: scale(.94);
}

@keyframes scale {
  100% {
    transform: scale();
  }
}

span {
  opacity: 0;
  filter: blur(4px);
  display: inline-block;
}

span:nth-child(1) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .1s forwards;
}

span:nth-child(2) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .2s forwards;
}

span:nth-child(3) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .3s forwards;
}

span:nth-child(4) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .4s forwards;
}

span:nth-child(5) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .5s forwards;
}

span:nth-child(6) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .6s forwards;
}

span:nth-child(7) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .7s forwards;
}

span:nth-child(8) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .8s forwards;
}

span:nth-child(9) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) .9s forwards;
}

span:nth-child(10) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1s forwards;
}

span:nth-child(11) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.1s forwards;
}

span:nth-child(12) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.2s forwards;
}

span:nth-child(13) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.3s forwards;
}

span:nth-child(14) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.4s forwards;
}

span:nth-child(15) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.5s forwards;
}

span:nth-child(16) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.6s forwards;
}

span:nth-child(17) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.7s forwards;
}

span:nth-child(18) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.8s forwards;
}

span:nth-child(19) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 1.9s forwards;
}

span:nth-child(20) {
  animation: fade-in .8s cubic-bezier(.11, 0, .5, 0) 2s forwards;
}

@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur();
  }
}

.circle-animation {
  width: 100%;
  height: 50px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #4169e1;
  border-radius: 50px;
  position: relative;
  overflow: hidden;
}

.circle-animation:hover, .circle-animation:focus {
  box-shadow: 0 2px 8px 1px rgba(65, 105, 225, .9);
}

.circle-animation div {
  width: 30px;
  height: 30px;
  background-color: red;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.circle-animation div:nth-child(1) {
  background-color: #4169e1;
  animation: move 2s cubic-bezier(.2, .64, .81, .23) infinite;
}

.circle-animation div:nth-child(2) {
  background-color: #4169e1;
  animation: move 2s cubic-bezier(.2, .64, .81, .23) .15s infinite;
}

.circle-animation div:nth-child(3) {
  background-color: #4169e1;
  animation: move 2s cubic-bezier(.2, .64, .81, .23) .3s infinite;
}

@keyframes move {
  0% {
    left: 0%;
  }

  100% {
    left: 100%;
  }
}

.history-search-wrapper {
  width: 100%;
  margin: 0;
}

.history-search {
  width: 80%;
  height: 100%;
  z-index: 100;
  background-color: #fff;
  border: 1px solid #4169e1;
  border-radius: 50px;
  align-items: center;
  margin: 10px auto 0;
  padding: 5px;
  display: flex;
  position: relative;
  overflow-x: scroll;
  overflow-y: hidden;
  box-shadow: 0 2px 8px 1px rgba(65, 105, 225, .9);
}

.history-search__aaa {
  height: 40px;
  color: #fff;
  cursor: pointer;
  background-color: #4169e1;
  border: none;
  border-radius: 50px;
  padding: 10px;
  font-size: small;
}

.history-search__aaa:hover {
  box-shadow: 0 2px 8px 1px rgba(65, 105, 225, .9);
}

.history-search__aaa:not(:last-child) {
  margin-right: 5px;
}

.history-search__title {
  min-width: 120px;
}

:root {
  --animate-duration: 1s;
  --animate-delay: 1s;
  --animate-repeat: 1;
}

.animate__animated {
  animation-duration: 1s;
  -webkit-animation-duration: var(--animate-duration);
  animation-duration: var(--animate-duration);
  animation-fill-mode: both;
}

.animate__animated.animate__infinite {
  animation-iteration-count: infinite;
}

.animate__animated.animate__repeat-1 {
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: var(--animate-repeat);
  animation-iteration-count: var(--animate-repeat);
}

.animate__animated.animate__repeat-2 {
  animation-iteration-count: 2;
  -webkit-animation-iteration-count: calc(var(--animate-repeat) * 2);
  animation-iteration-count: calc(var(--animate-repeat) * 2);
}

.animate__animated.animate__repeat-3 {
  animation-iteration-count: 3;
  -webkit-animation-iteration-count: calc(var(--animate-repeat) * 3);
  animation-iteration-count: calc(var(--animate-repeat) * 3);
}

.animate__animated.animate__delay-1s {
  animation-delay: 1s;
  -webkit-animation-delay: var(--animate-delay);
  animation-delay: var(--animate-delay);
}

.animate__animated.animate__delay-2s {
  animation-delay: 2s;
  -webkit-animation-delay: calc(var(--animate-delay) * 2);
  animation-delay: calc(var(--animate-delay) * 2);
}

.animate__animated.animate__delay-3s {
  animation-delay: 3s;
  -webkit-animation-delay: calc(var(--animate-delay) * 3);
  animation-delay: calc(var(--animate-delay) * 3);
}

.animate__animated.animate__delay-4s {
  animation-delay: 4s;
  -webkit-animation-delay: calc(var(--animate-delay) * 4);
  animation-delay: calc(var(--animate-delay) * 4);
}

.animate__animated.animate__delay-5s {
  animation-delay: 5s;
  -webkit-animation-delay: calc(var(--animate-delay) * 5);
  animation-delay: calc(var(--animate-delay) * 5);
}

.animate__animated.animate__faster {
  animation-duration: .5s;
  -webkit-animation-duration: calc(var(--animate-duration) / 2);
  animation-duration: calc(var(--animate-duration) / 2);
}

.animate__animated.animate__fast {
  animation-duration: .8s;
  -webkit-animation-duration: calc(var(--animate-duration) * .8);
  animation-duration: calc(var(--animate-duration) * .8);
}

.animate__animated.animate__slow {
  animation-duration: 2s;
  -webkit-animation-duration: calc(var(--animate-duration) * 2);
  animation-duration: calc(var(--animate-duration) * 2);
}

.animate__animated.animate__slower {
  animation-duration: 3s;
  -webkit-animation-duration: calc(var(--animate-duration) * 3);
  animation-duration: calc(var(--animate-duration) * 3);
}

@media print, (prefers-reduced-motion: reduce) {
  .animate__animated {
    transition-duration: 1ms !important;
    animation-duration: 1ms !important;
    animation-iteration-count: 1 !important;
  }

  .animate__animated[class*="Out"] {
    opacity: 0;
  }
}

@keyframes bounce {
  from, 20%, 53%, to {
    animation-timing-function: cubic-bezier(.215, .61, .355, 1);
    transform: translate3d(0, 0, 0);
  }

  40%, 43% {
    animation-timing-function: cubic-bezier(.755, .05, .855, .06);
    transform: translate3d(0, -30px, 0)scaleY(1.1);
  }

  70% {
    animation-timing-function: cubic-bezier(.755, .05, .855, .06);
    transform: translate3d(0, -15px, 0)scaleY(1.05);
  }

  80% {
    transition-timing-function: cubic-bezier(.215, .61, .355, 1);
    transform: translate3d(0, 0, 0)scaleY(.95);
  }

  90% {
    transform: translate3d(0, -4px, 0)scaleY(1.02);
  }
}

.animate__bounce {
  transform-origin: bottom;
  animation-name: bounce;
}

@keyframes flash {
  from, 50%, to {
    opacity: 1;
  }

  25%, 75% {
    opacity: 0;
  }
}

.animate__flash {
  animation-name: flash;
}

@keyframes pulse {
  from {
    transform: scale3d(1, 1, 1);
  }

  50% {
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

.animate__pulse {
  animation-name: pulse;
  animation-timing-function: ease-in-out;
}

@keyframes rubberBand {
  from {
    transform: scale3d(1, 1, 1);
  }

  30% {
    transform: scale3d(1.25, .75, 1);
  }

  40% {
    transform: scale3d(.75, 1.25, 1);
  }

  50% {
    transform: scale3d(1.15, .85, 1);
  }

  65% {
    transform: scale3d(.95, 1.05, 1);
  }

  75% {
    transform: scale3d(1.05, .95, 1);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

.animate__rubberBand {
  animation-name: rubberBand;
}

@keyframes shakeX {
  from, to {
    transform: translate3d(0, 0, 0);
  }

  10%, 30%, 50%, 70%, 90% {
    transform: translate3d(-10px, 0, 0);
  }

  20%, 40%, 60%, 80% {
    transform: translate3d(10px, 0, 0);
  }
}

.animate__shakeX {
  animation-name: shakeX;
}

@keyframes shakeY {
  from, to {
    transform: translate3d(0, 0, 0);
  }

  10%, 30%, 50%, 70%, 90% {
    transform: translate3d(0, -10px, 0);
  }

  20%, 40%, 60%, 80% {
    transform: translate3d(0, 10px, 0);
  }
}

.animate__shakeY {
  animation-name: shakeY;
}

@keyframes headShake {
  0% {
    transform: translateX(0);
  }

  6.5% {
    transform: translateX(-6px)rotateY(-9deg);
  }

  18.5% {
    transform: translateX(5px)rotateY(7deg);
  }

  31.5% {
    transform: translateX(-3px)rotateY(-5deg);
  }

  43.5% {
    transform: translateX(2px)rotateY(3deg);
  }

  50% {
    transform: translateX(0);
  }
}

.animate__headShake {
  animation-name: headShake;
  animation-timing-function: ease-in-out;
}

@keyframes swing {
  20% {
    transform: rotate3d(0, 0, 1, 15deg);
  }

  40% {
    transform: rotate3d(0, 0, 1, -10deg);
  }

  60% {
    transform: rotate3d(0, 0, 1, 5deg);
  }

  80% {
    transform: rotate3d(0, 0, 1, -5deg);
  }

  to {
    transform: rotate3d(0, 0, 1, 0deg);
  }
}

.animate__swing {
  transform-origin: top;
  animation-name: swing;
}

@keyframes tada {
  from {
    transform: scale3d(1, 1, 1);
  }

  10%, 20% {
    transform: scale3d(.9, .9, .9)rotate3d(0, 0, 1, -3deg);
  }

  30%, 50%, 70%, 90% {
    transform: scale3d(1.1, 1.1, 1.1)rotate3d(0, 0, 1, 3deg);
  }

  40%, 60%, 80% {
    transform: scale3d(1.1, 1.1, 1.1)rotate3d(0, 0, 1, -3deg);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

.animate__tada {
  animation-name: tada;
}

@keyframes wobble {
  from {
    transform: translate3d(0, 0, 0);
  }

  15% {
    transform: translate3d(-25%, 0, 0)rotate3d(0, 0, 1, -5deg);
  }

  30% {
    transform: translate3d(20%, 0, 0)rotate3d(0, 0, 1, 3deg);
  }

  45% {
    transform: translate3d(-15%, 0, 0)rotate3d(0, 0, 1, -3deg);
  }

  60% {
    transform: translate3d(10%, 0, 0)rotate3d(0, 0, 1, 2deg);
  }

  75% {
    transform: translate3d(-5%, 0, 0)rotate3d(0, 0, 1, -1deg);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.animate__wobble {
  animation-name: wobble;
}

@keyframes jello {
  from, 11.1%, to {
    transform: translate3d(0, 0, 0);
  }

  22.2% {
    transform: skewX(-12.5deg)skewY(-12.5deg);
  }

  33.3% {
    transform: skewX(6.25deg)skewY(6.25deg);
  }

  44.4% {
    transform: skewX(-3.125deg)skewY(-3.125deg);
  }

  55.5% {
    transform: skewX(1.5625deg)skewY(1.5625deg);
  }

  66.6% {
    transform: skewX(-.78125deg)skewY(-.78125deg);
  }

  77.7% {
    transform: skewX(.390625deg)skewY(.390625deg);
  }

  88.8% {
    transform: skewX(-.195313deg)skewY(-.195313deg);
  }
}

.animate__jello {
  transform-origin: center;
  animation-name: jello;
}

@keyframes heartBeat {
  0% {
    transform: scale(1);
  }

  14% {
    transform: scale(1.3);
  }

  28% {
    transform: scale(1);
  }

  42% {
