.start-page__upepr-title {
  color: royalblue;
  text-transform: uppercase;
  font-size: 100px;
  margin: 0;
}

.start-page {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: royalblue;
}

.hide {
  display: none;
}

.start-page__title {
  width: 100%;
  text-align: center;
  transform: scale(0.94);
  animation: scale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}
@keyframes scale {
  100% {
    transform: scale();
  }
}

