@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}

.circle-animation {
  position: relative;
  width: 100%;
  height: 50px;
  background-color: transparent;
  border: 1px solid royalblue;
  border-radius: 50px;
  overflow: hidden;
  cursor: pointer;
}

.circle-animation:hover,
.circle-animation:focus {
  box-shadow: 0 2px 8px 1px #4169e1e6;
}

.circle-animation div {
  width: 30px;
  height: 30px;
  position: absolute;
  background-color: red;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
}

.circle-animation div:nth-child(1) {
  background-color: royalblue;
  animation: move 2s infinite cubic-bezier(0.2, 0.64, 0.81, 0.23);
}
.circle-animation div:nth-child(2) {
  background-color: royalblue;
  animation: move 2s 150ms infinite cubic-bezier(0.2, 0.64, 0.81, 0.23);
}
.circle-animation div:nth-child(3) {
  background-color: royalblue;
  animation: move 2s 300ms infinite cubic-bezier(0.2, 0.64, 0.81, 0.23);
}

@keyframes move {
  0% {
    left: 0%;
  }
  100% {
    left: 100%;
  }
}
